@import 'variables';
@import 'mixins';

.logowarming-generator{
	background: #333333;
	// @include dropShadow;
	border-radius: 26px;
	max-width:500px;
	padding:50px 0 40px;
	margin:auto;
	display:flex;
	flex-direction:column;
	overflow: hidden;
	&.image-uploaded{
		#output{
			visibility: visible;
			opacity: 1;
		}
		#wrap{
			border:none;
		}
		.top-bar-controls{
			display:block;
			img{
				padding-top:0;
			}
		}
	}
	&.dragover{
		.drag-border{
			display:block;
			opacity:1;
		}
	}
	&.has-error{
		cursor:pointer;
		#wrap{
			#imgLoader{
				display:none;
			}
			#error{
				display:flex;
			}
		}
	}
	.drag-border{
		opacity:0;
		transition:opacity $cb .15s;
		position:absolute;
		top:0;
		bottom:0;
		left:0;
		right:0;
		width:100%;
		padding:20px;
	}
	.top-bar-controls{
		position:relative;
		padding:0 20px;
		display:none;
		z-index:10;
		.btn.white{
			width:auto;
			display:flex;
			justify-content: center;
			padding:14px 20px;
			background-color:$white;
			color:$black;
			top:-20px;
			border:1px solid #dadada;
			// box-shadow: 0 21px 22px 0 rgba(0,0,0,0.2);
			font-size:16px;
			img{
				margin-right:10px;
			}
		}
		#rotate-image{
			position:absolute;
			left:40px;
		}
		#reset-generator{
			position:absolute;
			right:40px;
		}
	}
	.hero-unit {
		margin: 50px auto 0 auto;
		width: 300px;
		font-size: 18px;
		font-weight: 200;
		line-height: 30px;
		background-color: #eee;
		border-radius: 6px;
		padding: 60px;
		h1 {
			font-size: 60px;
			line-height: 1;
			letter-spacing: -1px;
		}
	}
	#output{
		visibility: hidden;
		opacity: 0;
	    transition: opacity 1s ease-in-out;
	    -moz-transition: opacity 1s ease-in-out;
	    -webkit-transition: opacity 1s ease-in-out;
	}
	.canvas-main{
		width:500px;
		height:500px;
		position: relative;
		.canvas-container{
			position:absolute !important;
			top:0;
			left:0;
			#logo{
				z-index:10;
			}
			#overlay{
				z-index:15;
				background-blend-mode: overlay;
			}
		}
	}

	.slider{
		direction:rtl;
	}

	form{
		background-color:$white;
		position: relative;
		height:100%;
		cursor:pointer;
		&:after{
			content:"";
			position:absolute;
			width:100%;
			height:100%;
			// border: 1px dashed $blue;
			z-index:30;
			pointer-events: none;
			transition:border-color $cb .15s;
		}
		&.dragover{
			&:after{
				border-color:$blue;
			}
		}
	}
	input{
		opacity:0;
		top: 0;
		left: 0;
		height:500px;
		width:500px;
		position:absolute;
		z-index:10;
		cursor:pointer;
	}

	#wrap{
		position:relative;
		width:500px;
		height:500px;
		background-color:#FFFFFF;
		// border:25px solid #FFFFFF;
		border: 1px solid #dadada;
		.btn{
			svg{
				.fill{
					transition:stroke $cb .15s;
				}
			}
		}
		&:hover{
			.btn{
				background-color:$blue;
				color:$white;
				svg{
					.fill{
						stroke:$white;
					}
				}
			}
		}
		#drag-and-drop{
			display:block;
			position:absolute;
			top:0;
			bottom:0;
			left:0;
			right:0;
			margin:auto;
			text-align:center;
			height:290px;
			width:100%;
			z-index:5;
			font-family:$narrow;
			font-size:32px;
			line-height:48px;
			max-width:350px;
			color:#303030;
			div{
				margin:auto;
				margin-bottom:0px;
				height:120px;
			}
			span{
				display:block;
				font-family: $narrow;
				font-size: 30px;
				color: #969696;
				line-height: 46px;
			}
			button{
				margin:20px auto 0;
				width:auto;
			}
		}
		#error{
			display:flex;
			justify-content: center;
			align-items:center;
			position:absolute;
			top:0;
			bottom:0;
			left:0;
			right:0;
			margin:auto;
			display:block;
			text-align:center;
			height:100%;
			width:100%;
			z-index:5;
			font-family:$narrow;
			font-size:32px;
			line-height:48px;
			background-color:$white;
			text-align:center;
			display:none;
			.error-content{
				position: absolute;
				top: 0;
				bottom: 0;
				margin: auto;
				height: 65%;
			}
			.btn{
				margin:0 auto;
				justify-content: center;
			}
			.title{
				font-family: $bold;
				font-size: 85px;
				color: #175872;
				letter-spacing: -3.54px;
				display:flex;
				align-items:center;
				text-transform:uppercase;
				margin-bottom:20px;
				img{
					width:75px;
					padding-right:10px;
				}
			}
			.cant-melt{
				font-family: $narrow;
				font-weight:400;
				font-size: 24px;
				color: #303030;
				text-align: center;
				line-height: 31px;
				display:block;
				margin-bottom:10px;
			}
			.file-types{
				font-family: $serif;
				font-size: 16px;
				color: #303030;
				text-align: center;
				line-height: 22px;
				display:block;
				margin:0 auto 30px;
				max-width:310px;
			}
			#uploadedImg{
				left:-250px;
			}
		}
	}
	label {
		display: inline-block;
		color:$white;
		width: 130px;
	}
	.info {
		display: inline-block;
		width: 25px;
		background: #ffc;
	}
	.controls{
		padding:20px 40px 0;
		text-align:center;
		display:flex;
		flex-direction:column;
		button{
			align-self:flex-end;
		}
		label{
			font-family:$narrow;
			display:block;
			font-size:18px;
			margin:0 auto 20px;
		}
		.temperature-slider-container{
			display:flex;
			margin-bottom:20px;
			.low{
				margin-right:10px;
			}
			.high{
				margin-left:10px;
				height:36px;
			}
		}
	}
	@media(max-width:540px){
		width:100%;
		#output{
			width:100%;
		}

		.canvas-main, #wrap, #wrap #uploadedImg{
			width:calc(100vw - 40px);
			height:calc(100vw - 40px);
		}
		#wrap{
			display:flex;
			justify-content:center;
			align-items:center;
			align-content: center;
			padding:0;
			form{
				position: absolute;
				width:100%;
			}
			#drag-and-drop{
/*
				position: static;
				height:auto;
*/
				position: absolute;
			    height: 290px;
			}
		}
	}
	@media(max-width:480px){
		.top-bar-controls{
			#rotate-image{
				left:10px;
			}
			#reset-generator{
				right:10px;
			}
		}
		.drag-border{
			padding:10px;
		}
		#wrap{
			height:calc(100vw - 20px);
			width:calc(100vw - 20px);
			#drag-and-drop{
				font-size: 28px;
				position: absolute;
			    height: 220px;
				.icon-upload{
					height:60px;
				}
				span{
					font-size:28px;
					line-height:32px;
				}
			}
			#error {
				.title {
					font-size: 15vw;
					padding-right:20px;
				}
				.error-content {
					height: 90%;
				}
			}
		}
		.controls{
			padding:20px 20px 0;
		}
	}
	&.disabled{
		label{
			opacity:0.5;
		}
		.rangeslider__fill{
			background-color:#6E6E6E;
		}
		.rangeslider__handle{
			background-color:#DDDDDD;
			border-color:#C3C3C3;
		}
		.controls{
			pointer-events: none;
			cursor:not-allowed;
			.btn{
				background-color:#C3C3C3;
				opacity:.3;
			}
		}
	}
}