.header-container{
	position: relative;
}
.hero{
	background-image:url('../images/hero/hero-bg.jpg');
	background-size:cover;
	background-repeat:no-repeat;
	background-position:center bottom;
	color:$white;
	position: relative;
	padding:40px 0 0;
	text-align:center;
	z-index:5;
	nav{
		text-align:left;
		img{
			display:none;
		}
		canvas{
			width:120px;
			text-align:left;
			position: relative;
			top:40px;
		}
		@media(max-width:768px){
			canvas{
				top:0;
			}
		}
		@media(max-width:480px){
			text-align:center;
			canvas{
				display:none;
			}
			img{
				display:block;
				margin:0 auto 20px;
				max-width:110px;
			}
		}
	}
	h1{
		margin-bottom:10px;
	}
	.max-width{
		position:relative;
		.sunset{
			position: absolute;
			top:-40px;
			right:20px;
			width:60%;
			max-width:680px;
			&.mobile{
				display: none;
			}
			@media(max-width:480px){
				display:none;
				&.mobile{
					display:block;
					position:static;
					margin:0 auto;
					width:200px;
				}
			}
		}
	}
	.text-content{
		padding:200px 80px 0;
		position: relative;
		z-index:5;
		p{
			max-width:730px;
			margin:0 auto 60px;
			&.narrow{
				font-family:$narrow;
				font-size: 24px;
				color: #FFFFFF;
				text-align: center;
				line-height: 34px;
				margin-top:40px;
				margin:40px auto 20px;
				max-width:530px;
			}
		}
		.donate{
			margin-bottom:80px;
			display:inline-block;
		}
	}
	.ribbon{
		position:absolute;
		left:0;
		bottom:0;
		width:100%;
		height:auto;
		pointer-events:none;
	}
	.donate-container{
		display:flex;
		justify-content: flex-end;
		align-items:center;
		position:relative;
		margin-top:45px;
		img{
			display:block;
			position: absolute;
			left:0;
			right:0;
			bottom:0;
			top:0;
			margin:auto;
			animation-name: bounce;
			animation-timing-function: cubic-bezier(0.280, 0.840, 0.420, 1);
			animation-duration: 2s;
			animation-iteration-count: infinite;
			transform-origin: bottom;
		}
		span{
			display:block;
			margin-top:0;
			font-weight:400;
		}
	}

    @keyframes bounce {
        0%   { transform: scale(1,1)      translateY(0); }
        10%  { transform: scale(1.1,.9)   translateY(0); }
        30%  { transform: scale(.9,1.1)   translateY(-20px); }
        50%  { transform: scale(1.05,.95) translateY(0); }
        57%  { transform: scale(1,1)      translateY(-7px); }
        64%  { transform: scale(1,1)      translateY(0); }
        100% { transform: scale(1,1)      translateY(0); }
    }


	@media(max-width:980px){
		nav{
			padding-left:20px;
		}
	}
	@media(min-width:1400px){
		padding:40px 0 0;
	}
	@media(max-width:600px){
		.text-content{
			padding:0 40px 50px;
		}
	}
	@media(max-width:768px){
		.text-content{
			padding-top:80px;
		}
	}
	@media(max-width:550px){
		.text-content{
			padding-top:20px;
		}
	}
	@media(max-width:480px){
		nav{
			canvas{
				width:90px;
				margin-bottom:20px;
			}
		}
		.text-content{
    		padding: 0 40px 50px;
			margin-top:50px;
			p.narrow{
				font-size: 20px;
				line-height: 30px;
			}
		}
		.ribbon{
			width:200vw;
			left:-100vw;
		}
	}
}
.bottom-border{
	width:100%; //104%;
	position:absolute;
	bottom:-21vw;
	left:-2px;//-2%;
	height:auto;
	pointer-events: none;
	&.middle{
		z-index:4;
		bottom:-26vw;
		left:0vw;
	}
	&.back{
		z-index:3;
		bottom:-30vw;
	}
}