.replace-pic{
	position:relative;
	background-image:url('../images/replace-pic/bg.jpg');
	background-repeat:no-repeat;
	background-size:cover;
	z-index:1;
	margin-top:-25vw;
	padding:35vw 0 30vw;
	.border-top, .border-bottom{
		position:absolute;
		width:100%;
	}
	.border-top{
		top:-1px;
	}
	.border-bottom{
		bottom:-1px;
	}
	.max-width{
		text-align:center;
		padding:0 20px;
		.step{
			margin:auto;
			text-align:center;
		}
		p{
			max-width:700px;
			margin:0 auto 20px;
		}
	}
	.logo-demonstration{
		display:flex;
		max-width:550px;
		margin:0 auto;
		.logo{
			width:40%;
			flex:1 1 40%;
			align-self:center;
			position:relative;
			z-index:5;
			&.slide-in{
				z-index:1;
			}
		}
		.arrow{
			width:80px;
			flex:0 0 80px;
			padding:0 30px;
		}
	}
	@media(max-width:480px){
		padding:45vw 0 45vw;
		.logo-demonstration{
			.arrow{
				flex:0 0 30px;
				width:30px;
				padding:5px;
			}
		}
	}
}