::selection {
	background: $yellow;
}

h1{
	font-family:$bold;
	color:$yellow;
	text-transform:uppercase;
	font-size:40px;
}

h2{
	font-family: $bold;
	font-size: 40px;
	color: #121212;
	line-height: 40px;
	text-align:center;
	text-transform: uppercase;
}

h3{
	font-family: $narrow;
	font-size: 24px;
	color: #121212;
	text-align: center;
	line-height: 34px;
	font-weight:400;
}

p{
	font-family:$serif;
	font-size:16px;
	line-height:32px;
	text-align:center;
	em{
		font-weight:bold;
		text-transform:italic;
		text-decoration: underline;
	}
}

a{
	color:inherit;
	font-style:italic;
	font-weight:800;
}

strong{
	font-style:italic;
}

@media(max-width:480px){
	h1, h2{
		font-size:24px;
		line-height:28px;
	}
	p{
		font-size:14px;
		line-height:28px;
	}
}