@import 'variables';

.button-container{
	position: relative;
	display:inline-block;
	height:100%;
}

.btn{
	position: relative;
	z-index:1;
	color:$white;
	padding:14px 60px;
	font-family:$narrow;
	font-size:18px;
	border-radius:80px;
	border:none;
	width:250px;
	cursor:pointer;
	text-decoration:none;
	transition:transform .05s $cb, box-shadow .05s $cb;
	font-style: normal;
	font-weight:400;
	margin:auto;
	display:inline-block;
	&:focus, &:active, &:visited{
		outline:none;
	}
	&:active{
		transform:scale(.95, .95);
		// box-shadow: 0 15px 16px 0 rgba(0,0,0,0.1);
		& + .btn-ghost{
			transform:scale(.95, .95);
			// box-shadow: 0 15px 16px 0 rgba(0,0,0,0.1);
		}
	}
	&.primary{
		background-color:$red;
	}
	&.outline{
		background-color:transparent;
		border:2px solid $white;
		transition:all $cb .15s;
		&:hover{
			background-color:$white;
			color:black;
		}
		&.blue{
			color:$blue;
			border-color:$blue;
			display:flex;
			align-items:center;
			img, svg{
				margin-right:10px;
			}
			&:hover{
				color:$white;
				background-color:$blue;
				svg{
					.fill{
						stroke: $white;
					}
				}
			}
		}
		&.grey{
			border:2px solid #B6B6B6;
			color:#B6B6B6;
			&:disabled{
				background-color:transparent;
				&:hover{
					background-color:transparent;
					color:#B6B6B6;
				}
			}
			&.small{
				font-size: 16px;
				padding:5px 10px;
				width:auto;
				display:flex;
				align-items:center;
				img{
					margin-right:5px;
				}
			}
			&:hover{
				color:$white;
				background-color:#b6b6b6;
			}
		}
	}
	&:disabled{
		cursor:not-allowed;
		background-color:#cccccc;
		opacity:.3;
	}
}
.btn-ghost{
	width:250px;
	height:100%;
	// box-shadow: 0 21px 22px 0 rgba(0,0,0,0.20);
	position: absolute;
	top:0;
	left:0;
	right:0;
	margin:auto;
	border-radius:80px;
	z-index:0;
	transition:transform .05s $cb, box-shadow .05s $cb;
}
.svg-filters {
	position: absolute;
	visibility: hidden;
	width: 1px;
	height: 1px;
}

.btn, .sunset {
  .safari & {
	-webkit-filter: none!important;
	filter: none!important;
  }
}