section.footer{
	padding:100px 0;
	background-image:url('../images/footer/bg.jpg');
	background-size:cover;
	background-repeat:no-repeat;
	background-position:top center;
	color:$white;
	font-family:$narrow;
	.max-width{
		text-align:center;
		.rpcc{
			display:flex;
			justify-content:space-between;
			margin:0 20px;

			@media(max-width:560px){
				flex-direction:column;
				align-items:center;
			}

			.rpcc-item{
				display:flex;
				flex-direction:column;
				flex:0 0 235px;
				padding-left:50px;
				&:first-of-type{
					@media(max-width:560px){
						margin-top:0;
					}
				}

				&.rpcc-logo{
					flex:1 1 260px;
					padding-left:0;
					display:block;
					text-align:left;
					img{
						width:100%;
						max-width:260px;
					}
				}

				&.rpcc-copy{
					p{
						color:$teal;
						font-family:$narrow;
						text-align:left;
						font-size: 18px;
						line-height:24px;
						margin-top:0;
					}

					.btn.outline.white{
						width:100%;
						max-width:120px;
						padding-left:5px;
						padding-right:5px;
						font-size:16px;
						margin:0;
					}
				}
				@media(max-width:650px){
					flex:0 0 35%;
				}
				@media(max-width:560px){
					flex-direction:column;
					align-items:center;
					margin:30px auto;
					padding:0;
					&.rpcc-logo{
						flex:0 0 100%;
					}
					&.rpcc-copy{
						display:block;
						p{
							text-align:center;
						}
					}
				}

			}
		}

		.legal-and-social{
			display:flex;
			justify-content:space-between;
			margin-top:120px;

			@media(max-width:560px){
				flex-direction:column-reverse;
				margin-top:75px;
			}

			.legal{
				margin-left:0;
				margin-right:auto;
				padding-left:20px;

				@media(max-width:560px){
					margin:15px auto 0;
					padding:0;
				}
			}

			.social-links{
				margin-right:0;
				margin-left:auto;
				padding-right:20px;
				@media(max-width:560px){
					margin:0 auto 15px;
					padding:0;
				}

				a{
					color:$white;
					text-decoration:none;
					font-style:normal;
					font-weight:400;
					&:hover{
						text-decoration:underline;
					}
				}
			}
		}
	}
}