*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

body{
  color:#333333;
}

// Styling on warning for users on IE7 or below
.browsehappy {
  text-align: center;
  margin: 0;
  background: #fff8e2;
  color: #000;
  padding: 0.5em 0;
}

.max-width{
  margin:0 auto;
  width:100%;
  max-width:960px;
}

.step{
  width:122px;
}

.hide{
  visibility:hidden !important;
  opacity:0 !important;
  display:none !important;
}

html.sr .load-hidden{
  visibility: hidden;
}