.donate{
	position: relative;
	.donate-container{
		position: relative;
		z-index:5;
		padding:20px 0 200px;
		background-image:url('../images/donate/donate-bg-full.jpg');
		background-size:cover;
		background-repeat:no-repeat;
		background-position:top center;
	}
	.max-width{
		text-align:center;
		padding:0 20px;
		h2{
			color:#186282;
		}
		p{
			max-width:600px;
			margin:0 auto;
			margin-bottom:40px;
		}
		.btn{
			width:auto;
			display:inline-block;
		}
	}
}

.top-border{
	position:absolute;
	top:-20vw;
	pointer-events: none;
	user-select: none;
	&.middle{
		z-index:3;
		top:-26vw;
	}
	&.back{
		z-index:2;
		top:-30vw;
	}
}