.challenge{
	text-align:center;
	margin-top:-5vw;
	padding:0 20px 45vw;
	position: relative;
	z-index:10;
	h2{
		max-width:650px;
		margin-left:auto;
		margin-right:auto;
	}
	p{
		max-width:650px;
		margin:0 auto 50px;
	}
	h3{
		max-width:600px;
		margin:0 auto 50px;
	}
	.tweet-container{
		position:relative;
		max-width:700px;
		margin:auto;
		text-align:center;
		.tweet-info {
			.info-right {
				position: absolute;
				display: flex;
				left: -20px;
				width: 70px;
				height: 150px;
				justify-content: center;
				@media(max-width:768px){
					display: none;
				}	
				@media(min-width:1200px){
				   left: -20px;
				}
			}
			.info-down {
				width: 120px;
				@media(min-width:768px){
					display: none;
				}
			}
		}
		.tweet-box {
			background: #333333;
			// box-shadow: 0 40px 40px 0 rgba(0,0,0,0.30);
			border-radius: 26px;
			margin: 4vw 8vw 7vw;
			text-align: left;
			padding: 40px 30px;
			font-family: $narrow;
			font-size: 20px;
			font-weight: 300;
			color: #ffffff;
			line-height: 24px;
			span {
				display: inline;
				text-decoration: none;
				font-size: 20px;
				font-style: normal;
				font-weight: 300;
				line-height: 24px;
				color: #FFCD00;
			}
			@media(min-width:1200px){
			    margin: 4vw 80px 7vw;
			}
		}
		.updated{
			position: absolute;
			width:120px;
			left:50px;
			top:-40px;
		}
		.post-the-challenge{
			position:absolute;
			bottom:-50px;
			right:20px;
			width:112px;
		}
		.tweet{
			max-width:460px;
			width:100%;
			margin:0 auto;
		}
		span{
			display:block;
			font-family: $narrow;
			font-size: 30px;
			color: #303030;
			line-height: 46px;
			margin-top:-50px;
			margin-bottom:20px;
		}
		.buttons{
			display:flex;
			justify-content: space-between;
			max-width:345px;
			margin:auto;
			.button-container{
				width:calc(50% - 10px);
				flex:0 0 calc(50% - 10px);
				.btn{
					width:100%;
					display:block;
					padding-left:10px;
					padding-right:10px;
				}
				.btn-ghost{
					width:100%;
				}
			}
		}
	}
	@media(max-width:480px){
		padding:40px 0 180px;
		h2, p, h3{
			padding-left:20px;
			padding-right:20px;
		}
		.tweet-container{
			.tweet{
				display:block;
			}
			span{
				margin-top:-20px;
			}
			.buttons{
				padding:0 20px;
			}
		}
	}
}